.button {
  border: none;
  outline: none;
  box-sizing: border-box;
  padding: 0;

  &:hover {
    cursor: pointer;
  }
}

.sign-btn {
  color: #fff;
  background: none;
  display: grid;
  align-content: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  box-shadow: -1px 0px 0 0 rgba(255, 255, 255, 0.1);

  &:hover {
    background-color: #ff7373;
  }
}

.btn-model,
.btn-discover,
.btn-view,
.filter-btn_active {
  font: 16px / normal $fnt_Bold;
  text-transform: uppercase;
  line-height: 1.63;
}

.btn-model {
  margin-top: 31.7px;
  max-width: 225px;
  width: 100vw;
  height: 48px;
  background-color: #ff7373;
  color: #fff;
}

.btn-discover {
  margin-top: 25.5px;
  width: 145px;
  height: 48px;
  background: #623232;
  color: #fff;
}

.btn-view {
  margin: 317.8px auto 50px auto;
  width: 211px;
  height: 48px;
  background-color: #4bd8cd;
  color: #000000;
}

.filter-btn {
  background: none;
  font-size: 30px;
  color: #bdb9b5;
  text-transform: uppercase;
}

.filter-btn_active {
  color: #623232;
}

.btn-connection {
  margin: 0;
  width: 211px;
  height: 48px;
}
